import React from 'react';
import { api } from '../utils/Api'
import { Switch, Route, Redirect, useHistory } from 'react-router-dom';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import ImagePopup from './ImagePopup';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';
import ConfirmPopup from './ConfirmPopup';
import Register from './Register';
import Login from './Login';
import InfoTooltip from './InfoTooltip';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import ProtectedRoute from './ProtectedRoute';
import { register, authorize, getContent } from '../utils/auth';
import imgSuccess from '../images/Union_success.png'
import imgFail from '../images/Union_fail.png';

function App() {

  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [selectedCard, setSelectedCard] = React.useState({});
  const [isImagePopupOpen, setIsImagePopupOpen] = React.useState(false);
  const [currentUser, set–°urrentUser] = React.useState({});
  const [cards, setCards] = React.useState([]);
  const [isConfirmPopupOpen, setIsConfirmPopupOpen] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const [loggedIn, setloggedIn] = React.useState(false);
  const [message, setMessage] = React.useState('');
  const [isInfoTooltipPopupOpen, setIsInfoTooltipPopupOpen] = React.useState(false);
  const [email, setEmail] = React.useState('');
  const [infoTooltipImage, setInfoTooltipImage] = React.useState(imgSuccess);


  const history = useHistory();

  function handleRegisterSubmit(password, email) {
    register(password, email)
      .then((res) => {
        if (res.data) {
          setMessage('–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!');
          setIsInfoTooltipPopupOpen(true);
          setInfoTooltipImage(imgSuccess);
          history.push('/signin')
        }
        if (res.error) {
          setMessage('–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.');
          setInfoTooltipImage(imgFail);
        }
        if (res.message) {
          setMessage('–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.');
          setInfoTooltipImage(imgFail);
        }
      })
      .then(() => {
        setIsInfoTooltipPopupOpen(true);
      })
      .catch((err) => {
        console.log(err);
      });
  }
  const handleLogin = (password, email) => {

    authorize(password, email)

      .then((res) => {
        if (res.token) {
          console.log(res.token)
          localStorage.setItem('token', res.token);
          setEmail(email);
          setloggedIn(true);
          history.push('/');
          return;
        }
      })
      .catch((res) => {
        console.log(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑. ${res.message}`);
        setMessage(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑. ${res.message}`);
        setInfoTooltipImage(imgFail);;
        setIsInfoTooltipPopupOpen(true);
      })
  }

  const tokenCheck = () => {
    const token = localStorage.getItem('token');

    if (!token) {
      return;
    }

    getContent(token).then((res) => {
      if (res) {
        console.log(res)
        const email = res.data.email;
        setloggedIn(true);
        setEmail(email);
        history.push('/')
      }
    });
  }

  React.useEffect(() => {
    tokenCheck();
  }, [localStorage]);

  // React.useEffect(() => {
  //   const token = localStorage.getItem('token');
  //   if (token) {
  //     setloggedIn(true);
  //   }
  // }, [])

  function signOut() {
    localStorage.removeItem('token');
    setloggedIn(false);
    history.push('/signin');
  }


  React.useEffect(() => {
    Promise.all([api.getUserInfo(), api.getInitialCards()])
      .then(([userInfo, initialCards]) => {
        set–°urrentUser(userInfo)
        setCards(initialCards);
      })
      .catch((error) => console.log('–û—à–∏–±–∫–∞ –∑–∞–ø—Ä–æ—Å–∞ - ' + error))

  }, []);

  function handleUpdateUser({ name, about }) {
    setIsLoading(true)
    api.setUserInfo({ name, about })
      .then((userInfo) => {
        set–°urrentUser(userInfo);
        closeAllPopups()
      })
      .catch(err => console.error(err))


  }

  function handleUpdateAvatar({ avatar }) {
    setIsLoading(true)
    api.setUserAvatar({ avatar })
      .then((userInfo) => {
        set–°urrentUser(userInfo);
        closeAllPopups()
      }
      )
      .catch(err => console.error(err))


  }


  function handleCardLike(card) {

    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const isLiked = card.likes.some(i => i._id === currentUser._id);

    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    api.putLike(card._id, !isLiked).then((newCard) => {
      // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
      const newCards = cards.map((c) => c._id === card._id ? newCard : c);
      // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
      setCards(newCards);
    })
      .catch(err => console.error(err));

    api.removeLike(card._id, isLiked).then((newCard) => {
      // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
      const newCards = cards.map((c) => c._id === card._id ? newCard : c);
      // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
      setCards(newCards);
    })
      .catch(err => console.error(err));
  }


  const handleCardDelete = () => {
    //setIsLoading(true)
    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    api.deleteCard(selectedCard._id)
      .then(() => {
        const newCards = cards.filter((item) => item._id !== selectedCard._id);
        setCards(newCards);
        closeAllPopups();
      })
      .catch((error) => console.log('–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ : ', error))

  }

  function handleAddPlaceSubmit({ name, link, alt }) {
    setIsLoading(true)
    api.addCard({ name, link, alt })
      .then((newCard) => {
        setCards([newCard, ...cards]);
        closeAllPopups();
      })
      .catch((error) => console.log('–û—à–∏–±–∫–∞ –∑–∞–ø—Ä–æ—Å–∞ - ' + error))
  }


  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true);
  }

  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true);
  }

  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(true);
  }


  function closeAllPopups() {
    setIsEditProfilePopupOpen(false);
    setIsAddPlacePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setSelectedCard({});
    setIsImagePopupOpen(false);
    setIsConfirmPopupOpen(false);
    setIsLoading(false);
    setIsInfoTooltipPopupOpen(false);
  }

  const handleCardConfirm = (card) => {
    setSelectedCard(card);
    setIsConfirmPopupOpen(true);

  }

  const handleCardClick = (card) => {
    setSelectedCard(card)
    setIsImagePopupOpen(true);
  }

  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page">

        <Header email={email} loggedIn={loggedIn} loggedOut={signOut} />

        <Switch>

          <Route path="/signin">
            <Login onLogin={handleLogin} />
          </Route>

          <Route path="/signup">
            <Register onRegister={handleRegisterSubmit} />
          </Route>

          <ProtectedRoute path='/'
            component={Main}
            onEditAvatar={handleEditAvatarClick}
            onEditProfile={handleEditProfileClick}
            onAddPlace={handleAddPlaceClick}
            onCardClick={handleCardClick}
            cards={cards}
            onCardLike={handleCardLike}
            onCardDelete={handleCardConfirm}
            loggedIn={loggedIn}
            value={currentUser}

          />
          
          <Route path="/">
            {loggedIn ? <Redirect to="/" /> : <Redirect to="/signin" />}
          </Route>

        </Switch>

        {loggedIn && <Footer />}

        {currentUser && <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups}
          onUpdateUser={handleUpdateUser}
          buttonText={isLoading ? '–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...' : '–°–æ—Ö—Ä–∞–Ω–∏—Ç—å'} />}

        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups}
          onAddCards={handleAddPlaceSubmit}
          buttonText={isLoading ? '–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...' : '–°–æ–∑–¥–∞—Ç—å'} />

        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar}
          buttonText={isLoading ? '–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...' : '–°–æ—Ö—Ä–∞–Ω–∏—Ç—å'} />

        <ImagePopup
          card={selectedCard}
          isOpen={isImagePopupOpen}
          onClose={closeAllPopups} />

        <ConfirmPopup
          onConfirm={handleCardDelete}
          onClose={closeAllPopups}
          isOpen={isConfirmPopupOpen}
          buttonText={isLoading ? '–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...' : '–î–∞'}
        />
        <InfoTooltip
          onClose={closeAllPopups}
          isOpen={isInfoTooltipPopupOpen}
          loggedIn={loggedIn}
          image={infoTooltipImage}
          message={message}
        />

      </div>
    </CurrentUserContext.Provider >
  );
}

export default App;
