import React from 'react';
import { Link } from 'react-router-dom';

function Login(props) {

    const [userData, setUserData] = React.useState({ password: '', email: '' });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setUserData({
            ...userData, [name]: value
        });
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        props.onLogin(userData.password, userData.email);
        setUserData({ email: '', password: '' });

    }

    return (
        <>
            <section className="login">
                <div className="login__container">
                    <h3 className="login__title">–í—Ö–æ–¥</h3>
                    <form onSubmit={handleSubmit} className="login__form" name="login">
                        <input onChange={handleChange} id="email" type="email" name="email" className="login__input-email" placeholder="Email" />
                        <input onChange={handleChange} id="password" type="password" name="password" className="login__input-password" placeholder="–ü–∞—Ä–æ–ª—å" />
                        <button type="submit" className="login__button-submit">–í–æ–π—Ç–∏</button>
                        <p>–ï—â—ë –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? <Link to="signup" className="login__signup">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Link></p>
                    </form>
                </div>

            </section>
        </>

    );
}

export default Login;