import React from 'react';
import { Route, Redirect } from "react-router-dom";

const ProtectedRoute = ({ component: Component, ...props }) => {
    return (
        <Route>
            {
                // –ï—Å–ª–∏ –∑–Ω–∞—á–µ–Ω–∏–µ loggedIn ‚Äî true, Route –ª–∏–±–æ –æ—Ç—Ä–∏—Å—É–µ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç, –∫–æ—Ç–æ—Ä—ã–π –ø–µ—Ä–µ–¥–∞–Ω HOC-–∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É –∫–∞–∫ –ø—Ä–æ–ø—Å,
                // –≤–∫–ª—é—á–∞—è –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–µ –ø—Ä–æ–ø—Å—ã, –ª–∏–±–æ –≤–µ—Ä–Ω—ë—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç Redirect –∏ –ø–µ—Ä–µ–∞–¥—Ä–µ—Å—É–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏. 
                () => props.loggedIn ? <Component {...props} /> : <Redirect to="./signin" />
            }
        </Route>
    )
};

export default ProtectedRoute;