import React from 'react';
import { Link } from 'react-router-dom';

function Register({ onRegister }) {

    const [userData, setUserData] = React.useState({ password: '', email: '' });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setUserData({
            ...userData, [name]: value
        });
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        onRegister(userData.password, userData.email)
    }

    return (
        <>
            <section className="login">
                <div className="login__container">
                    <h3 className="login__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h3>
                    <form className="login__form" name="login" onSubmit={handleSubmit}>
                        <input onChange={handleChange} value={userData.email} id="email" type="email" name="email" className="login__input-email" placeholder="Email" />
                        <input onChange={handleChange} value={userData.password} id="password" type="password" name="password" className="login__input-password" placeholder="–ü–∞—Ä–æ–ª—å" />
                        <button type="submit" className="login__button-submit">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
                        <div className="login__block-signup">
                            <p>–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? <Link to="signin" className="login__signup">–í–æ–π—Ç–∏</Link></p>
                        </div>
                    </form>
                </div>

            </section>
        </>

    );
}

export default Register;